@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{font-family:"Open Sans",sans-serif}.map_container{position:relative;margin:2rem;height:70vh;overflow-x:hidden;overflow-y:hidden}.map_container .next_step_container{position:absolute;margin:0 0 0 20px;bottom:25px;right:25px}.map_container .search_container{position:absolute;margin:20px 0 0 0;top:0;left:0;width:100%;z-index:30;border:none;display:flex;justify-content:center}.map_container .search_container input{width:80%;padding:.2rem .5rem;border:none;box-shadow:0 0 .5rem rgba(0,0,0,.2)}@media(max-width: 992px){.map_container{height:70vh;margin:0}}.my_button{border:none;font-size:.9rem;background-color:#fff;padding:.5rem 1rem;box-shadow:0 0 .5rem rgba(0,0,0,.2)}.my_button .small_button{padding:0}ul{background-color:#fff !important;list-style:none;padding-left:0 !important}ul li{cursor:pointer;transition:all .3s ease-in-out;padding:.2rem .5rem}ul li:hover{background-color:#ccc}.form_parameters{margin-top:10vh}.form_parameters .form-group{margin:15px 0}.form_parameters .form-group select,.form_parameters .form-group input{border-radius:0}.results{overflow:hidden;margin-top:10vh}
